import { observer, inject } from 'mobx-react';


import React, { useEffect } from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import {
  Typography,
  MenuItem,
  FormControl,
  Select,
  InputBase,
  Button,
  SwipeableDrawer
} from '@material-ui/core';
import theme from '../theme'
import MangaList from '../Components/mangaList'
import Filter from '../Components/filter'
import useInfiniteScroll from "../Components/useInfiniteScroll";
import sortParams from '../sort.params'
import Hidden from '@material-ui/core/Hidden';
const FilterInput = withStyles({root:{padding: "1.5px 4px 0 4px"},selectMenu: {margin: theme.spacing(1),}})(InputBase);
const useStylesMenu = makeStyles({
  list: {
    width: 250,
  },
  fullList: {
    width: 'auto',
  },
});
const RMenu = ({mangaFilterStore}) => {
  const classes = useStylesMenu();
  const [state, setState] = React.useState({
    right: false,
  });

  const toggleDrawer = (anchor, open) => (event) => {
    if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const list = (anchor) => (
    <div style={{width: 320}}>
      <div style={{padding:16}}>
      <Typography component={'span'} color="textSecondary" style={{fontWeight: "450", margin:16}}>
          <div style={{padding:4}}>Фильтр ({mangaFilterStore.cnt||0})</div>
          { 
          <Button onClick={()=>mangaFilterStore.clearAll("MangaListStore")} disabled={mangaFilterStore.cnt===0} className={classes.filterResetButton} variant="outlined" size="small" color="secondary">
            Сбросить
          </Button>
          }
        </Typography>
      </div>
      <Filter mangaFilterStore={mangaFilterStore} storeName={"MangaListStore"}/>
    </div>
  );

  return (
    <div>
      {
        <React.Fragment key={'right'}>
          <Button onClick={toggleDrawer('right', true)}>Фильтр ({mangaFilterStore.cnt||0})</Button>
          <SwipeableDrawer
            anchor={'right'}
            open={state['right']}
            onClose={toggleDrawer('right', false)}
            onOpen={toggleDrawer('right', true)}
          >
            {list('right')}
          </SwipeableDrawer>
        </React.Fragment>
      }
    </div>
  );
}


const useStyles = makeStyles(theme => ({
  formControl: {
    minWidth: 80,
    paddingLeft:4,
  },
  filterResetButton: {
    marginLeft: theme.spacing(1),
  },
  topGroup: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    paddingTop:72,
    marginBottom: theme.spacing(2),
  }
}));

const MangaListPage = observer(({mangaFilterStore, mangaListStore}) => {
  const classes = useStyles();
  useEffect(()=>{
    mangaListStore.fetchMangaChank()
    return mangaListStore.clear
  },[])
  const [isFetching, setIsFetching] = useInfiniteScroll(mangaListStore.nextMangaChank);
  useEffect(()=>{
    if(isFetching!==mangaListStore.isLoading){
      setIsFetching(mangaListStore.isLoading)
    }
  },[mangaListStore.isLoading,isFetching])
  return(
    <>
    <div className={classes.topGroup}>
      <Hidden xsDown>
        <Typography color="textPrimary" variant="h6" component={'span'}>
          Список манги <Typography variant="h6" color="textSecondary" component={'span'}> ({mangaListStore.total}) </Typography>
        </Typography>
      </Hidden>
      <Typography color="textSecondary" component={'span'} style={{fontWeight: "450", display: "flex", alignItems: "baseline"}}>
        Сортировка по:

        <FormControl className={classes.formControl}>
          <Select
            textColor="primary"
            value={mangaListStore.sortBy}
            onChange={evt=>mangaListStore.setSortBy(evt.target.value)}

            input={<FilterInput />}
          >
            {
              sortParams.map(sortParam=><MenuItem id={sortParam.value} value={sortParam.value}>{sortParam.name}</MenuItem>)
            }
          </Select>
          </FormControl>
      </Typography>
        <Hidden mdUp>
          <RMenu mangaFilterStore={mangaFilterStore}/>
        </Hidden>
      <Hidden smDown>   
        <Typography component={'span'} color="textSecondary" style={{fontWeight: "450", width: 234}}>
          Фильтр ({mangaFilterStore.cnt||0})
          { 
          <Button onClick={()=>mangaFilterStore.clearAll("MangaListStore")} disabled={mangaFilterStore.cnt===0} className={classes.filterResetButton} variant="outlined" size="small" color="secondary">
            Сбросить
          </Button>
          }
        </Typography>
      </Hidden> 
    </div>
    <div style={{display: "flex"}}>
    <Hidden mdUp>
      <div style={{width: "100%"}}>
        <MangaList list={mangaListStore.list} isLoading={mangaListStore.isLoading} skeletonNum={11}/>
      </div>
    </Hidden>

      <Hidden smDown>
        <div style={{width: "calc(100% - 250px)"}}>
          <MangaList list={mangaListStore.list} isLoading={mangaListStore.isLoading} skeletonNum={11}/>
        </div>
        <div style={{width: 250}}>
          <Filter mangaFilterStore={mangaFilterStore} storeName={"MangaListStore"}/>
        </div>
      </Hidden>
    </div>
    </>
)})



export default inject("mangaFilterStore", "mangaListStore")(MangaListPage);


