import React from 'react';
import Checkbox from '../checkbox'
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import { observer, inject } from 'mobx-react'




const useStyles = makeStyles(theme => ({
    filterGroup:{
        paddingBottom: theme.spacing(1.5),
        paddingLeft: theme.spacing(2)
    },
}))



const filter = observer(({mangaFilterStore, storeName}) => {
    const classes = useStyles();
    const translate = {
        status:"Статус перевода",
        type:"Тип",
        format:"Формат выпуска",
        genre:"Жанр",
    }
    return (
        <>  
        {
            Object.keys(mangaFilterStore.filter).map(type=>{
                    const max = type==='genre'? 2 : 1
                    return (
                        <div key={type} className={classes.filterGroup}>
                        <Typography color="textPrimary" variant="body1">{translate[type]}</Typography>
                        {   
                            mangaFilterStore.filter[type].map((el,index)=><Checkbox key={`${type}_${el.id}`} max={max} {...el} handler={()=>mangaFilterStore.changeFilter(type,index,max,storeName)} />)
                        }
                        </div>
                    )
                }
            )
        }
        </>
    );
})
export default filter