import React from 'react';
import Grid from '@material-ui/core/Grid';
import Skeleton from '@material-ui/lab/Skeleton';
import { Link } from "react-router-dom";
import Manga from '../mangaCard';
import { Routes } from '../../Routes'
// import props from './../mangaCard/index';

export default props => {
    console.log(props)
    return (
        <>
        <Grid style={props.style} container spacing={2}>
        {
            props.list.map(el=>
                <Grid  key={el.id} item xs={4} md={3} lg={2} >
                    <Link to={Routes.Manga.link(el.id)}>
                        <Manga src={el.imgUrl} load={false}
                        title={`${el.title} | ${el.englishTitle}`} 
                        subtitle={`${{1:'Ongoing',2:'Completed',3:'Paused'}[el.status.id]}, ` + (el.lastChapter ? `глава ${el.lastChapter.volume} - ${el.lastChapter.chapter}` : 'глав нет')}/>
                    </Link>
                </Grid>
            )
        }
        {props.isLoading&&
            [...Array(props.skeletonNum)].map((_,i)=>
                <Grid  key={i} item xs={4} md={3} lg={2} >
                    <Manga load />
                </Grid>
            )
        }
        </Grid>
        </>
    );
}